.sendForm {
  border-radius: 20px;
  width: 976px;
  height: 40px;

  box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1),
    0 5px 20px 0 rgba(29, 33, 38, 0.03);
  background: #f5f7fb;
}

.textarea {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.6);

  margin: 12px 24px 0;

  resize: none;
  border: unset;

  background: transparent;
  overflow: hidden;
}

.button {
  border: none;
  border-radius: 20px;
  width: 120px;
  height: 40px;

  background: #06c;

  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
}

.button:hover {
}
.button:active {
}
